<template>
  <div class="home">
    <h2>Home</h2>
    <div class>{{greeting}}</div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "home",
  data() {
    return {
      greeting: ""
    };
  },
  created() {
    this.$http
      .get("/api/home")
      .then(res => (this.greeting = res.data.greeting));
  }

  // components: {
